'use client';

import React, { useState, useEffect } from 'react';
import { 
  collection, 
  addDoc, 
  getDocs, 
  doc, 
  getDoc, 
  updateDoc, 
  deleteDoc, 
  onSnapshot,
  query,
  where,
  orderBy,
  limit,
  serverTimestamp,
  Timestamp
} from 'firebase/firestore';
import { db } from '../lib/firebase';

interface TestUser {
  id?: string;
  name: string;
  email: string;
  age: number;
  createdAt?: Timestamp;
}

export default function FirestoreTest() {
  const [users, setUsers] = useState<TestUser[]>([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [newUser, setNewUser] = useState({
    name: '',
    email: '',
    age: 0
  });

  // Test 1: Add a new document
  const addUser = async () => {
    try {
      setLoading(true);
      setMessage('Adding user...');
      
      const docRef = await addDoc(collection(db, 'testUsers'), {
        name: newUser.name,
        email: newUser.email,
        age: newUser.age,
        createdAt: serverTimestamp()
      });
      
      setMessage(`User added successfully with ID: ${docRef.id}`);
      setNewUser({ name: '', email: '', age: 0 });
      
      // Refresh the list
      await fetchUsers();
    } catch (error) {
      setMessage(`Error adding user: ${error}`);
      console.error('Error adding document: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 2: Fetch all documents
  const fetchUsers = async () => {
    try {
      setLoading(true);
      setMessage('Fetching users...');
      
      const querySnapshot = await getDocs(collection(db, 'testUsers'));
      const usersList: TestUser[] = [];
      
      querySnapshot.forEach((doc) => {
        usersList.push({
          id: doc.id,
          ...doc.data()
        } as TestUser);
      });
      
      setUsers(usersList);
      setMessage(`Fetched ${usersList.length} users successfully`);
    } catch (error) {
      setMessage(`Error fetching users: ${error}`);
      console.error('Error getting documents: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 3: Update a document
  const updateUser = async (userId: string, newName: string) => {
    try {
      setLoading(true);
      setMessage('Updating user...');
      
      const userRef = doc(db, 'testUsers', userId);
      await updateDoc(userRef, {
        name: newName,
        updatedAt: serverTimestamp()
      });
      
      setMessage('User updated successfully');
      await fetchUsers();
    } catch (error) {
      setMessage(`Error updating user: ${error}`);
      console.error('Error updating document: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 4: Delete a document
  const deleteUser = async (userId: string) => {
    try {
      setLoading(true);
      setMessage('Deleting user...');
      
      await deleteDoc(doc(db, 'testUsers', userId));
      
      setMessage('User deleted successfully');
      await fetchUsers();
    } catch (error) {
      setMessage(`Error deleting user: ${error}`);
      console.error('Error deleting document: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 5: Query with conditions
  const fetchAdults = async () => {
    try {
      setLoading(true);
      setMessage('Fetching adults (age >= 18)...');
      
      const q = query(
        collection(db, 'testUsers'),
        where('age', '>=', 18),
        orderBy('age', 'desc'),
        limit(10)
      );
      
      const querySnapshot = await getDocs(q);
      const adultsList: TestUser[] = [];
      
      querySnapshot.forEach((doc) => {
        adultsList.push({
          id: doc.id,
          ...doc.data()
        } as TestUser);
      });
      
      setUsers(adultsList);
      setMessage(`Found ${adultsList.length} adults`);
    } catch (error) {
      setMessage(`Error fetching adults: ${error}`);
      console.error('Error querying documents: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 6: Real-time listener
  const startRealtimeListener = () => {
    setMessage('Starting real-time listener...');
    
    const unsubscribe = onSnapshot(
      collection(db, 'testUsers'),
      (snapshot) => {
        const usersList: TestUser[] = [];
        snapshot.forEach((doc) => {
          usersList.push({
            id: doc.id,
            ...doc.data()
          } as TestUser);
        });
        
        setUsers(usersList);
        setMessage(`Real-time update: ${usersList.length} users`);
      },
      (error) => {
        setMessage(`Real-time listener error: ${error}`);
        console.error('Real-time listener error: ', error);
      }
    );

    // Store unsubscribe function to stop listener later
    (window as any).firestoreUnsubscribe = unsubscribe;
  };

  // Test 7: Stop real-time listener
  const stopRealtimeListener = () => {
    if ((window as any).firestoreUnsubscribe) {
      (window as any).firestoreUnsubscribe();
      setMessage('Real-time listener stopped');
    }
  };

  // Test 8: Add sample data
  const addSampleData = async () => {
    try {
      setLoading(true);
      setMessage('Adding sample data...');
      
      const sampleUsers = [
        { name: 'Alice Johnson', email: 'alice@example.com', age: 25 },
        { name: 'Bob Smith', email: 'bob@example.com', age: 17 },
        { name: 'Charlie Brown', email: 'charlie@example.com', age: 30 },
        { name: 'Diana Prince', email: 'diana@example.com', age: 28 },
        { name: 'Eve Wilson', email: 'eve@example.com', age: 16 }
      ];

      for (const user of sampleUsers) {
        await addDoc(collection(db, 'testUsers'), {
          ...user,
          createdAt: serverTimestamp()
        });
      }
      
      setMessage('Sample data added successfully');
      await fetchUsers();
    } catch (error) {
      setMessage(`Error adding sample data: ${error}`);
      console.error('Error adding sample data: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Test 9: Clear all data
  const clearAllData = async () => {
    try {
      setLoading(true);
      setMessage('Clearing all data...');
      
      const querySnapshot = await getDocs(collection(db, 'testUsers'));
      const deletePromises = querySnapshot.docs.map(doc => deleteDoc(doc.ref));
      
      await Promise.all(deletePromises);
      
      setMessage('All data cleared successfully');
      setUsers([]);
    } catch (error) {
      setMessage(`Error clearing data: ${error}`);
      console.error('Error clearing data: ', error);
    } finally {
      setLoading(false);
    }
  };

  // Load users on component mount
  useEffect(() => {
    fetchUsers();
  }, []);

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white">
      <h1 className="text-3xl font-bold mb-6 text-center">ðŸ”¥ Firestore Testing Dashboard</h1>
      
      {/* Status Message */}
      <div className="mb-6 p-4 bg-gray-100 rounded-lg">
        <p className="text-sm">
          <strong>Status:</strong> {message || 'Ready to test Firestore operations'}
        </p>
        {loading && (
          <div className="mt-2">
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-500 inline-block"></div>
            <span className="ml-2 text-sm">Loading...</span>
          </div>
        )}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Left Column: Controls */}
        <div className="space-y-6">
          {/* Add New User Form */}
          <div className="bg-blue-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">âž• Add New User</h2>
            <div className="space-y-3">
              <input
                type="text"
                placeholder="Name"
                value={newUser.name}
                onChange={(e) => setNewUser(prev => ({ ...prev, name: e.target.value }))}
                className="w-full p-2 border rounded"
              />
              <input
                type="email"
                placeholder="Email"
                value={newUser.email}
                onChange={(e) => setNewUser(prev => ({ ...prev, email: e.target.value }))}
                className="w-full p-2 border rounded"
              />
              <input
                type="number"
                placeholder="Age"
                value={newUser.age || ''}
                onChange={(e) => setNewUser(prev => ({ ...prev, age: parseInt(e.target.value) || 0 }))}
                className="w-full p-2 border rounded"
              />
              <button
                onClick={addUser}
                disabled={loading || !newUser.name || !newUser.email}
                className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600 disabled:opacity-50"
              >
                Add User
              </button>
            </div>
          </div>

          {/* Test Operations */}
          <div className="bg-green-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">ðŸ§ª Test Operations</h2>
            <div className="grid grid-cols-2 gap-2">
              <button
                onClick={fetchUsers}
                disabled={loading}
                className="bg-green-500 text-white p-2 rounded hover:bg-green-600 disabled:opacity-50 text-sm"
              >
                Fetch All Users
              </button>
              <button
                onClick={fetchAdults}
                disabled={loading}
                className="bg-green-500 text-white p-2 rounded hover:bg-green-600 disabled:opacity-50 text-sm"
              >
                Fetch Adults (â‰¥18)
              </button>
              <button
                onClick={startRealtimeListener}
                disabled={loading}
                className="bg-purple-500 text-white p-2 rounded hover:bg-purple-600 disabled:opacity-50 text-sm"
              >
                Start Real-time
              </button>
              <button
                onClick={stopRealtimeListener}
                disabled={loading}
                className="bg-purple-700 text-white p-2 rounded hover:bg-purple-800 disabled:opacity-50 text-sm"
              >
                Stop Real-time
              </button>
              <button
                onClick={addSampleData}
                disabled={loading}
                className="bg-yellow-500 text-white p-2 rounded hover:bg-yellow-600 disabled:opacity-50 text-sm"
              >
                Add Sample Data
              </button>
              <button
                onClick={clearAllData}
                disabled={loading}
                className="bg-red-500 text-white p-2 rounded hover:bg-red-600 disabled:opacity-50 text-sm"
              >
                Clear All Data
              </button>
            </div>
          </div>

          {/* Connection Test */}
          <div className="bg-yellow-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">ðŸ”— Connection Info</h2>
            <div className="text-sm space-y-1">
              <p><strong>Project ID:</strong> studio-5063047183-aa053</p>
              <p><strong>Collection:</strong> testUsers</p>
              <p><strong>Total Users:</strong> {users.length}</p>
              <p><strong>Firebase Status:</strong> 
                <span className="text-green-600 ml-1">âœ… Connected</span>
              </p>
            </div>
          </div>
        </div>

        {/* Right Column: Users List */}
        <div className="bg-gray-50 p-4 rounded-lg">
          <h2 className="text-xl font-semibold mb-4">ðŸ‘¥ Users List ({users.length})</h2>
          
          {users.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p>No users found</p>
              <p className="text-sm mt-2">Add some users or load sample data to get started</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-96 overflow-y-auto">
              {users.map((user) => (
                <div key={user.id} className="bg-white p-3 rounded border">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h3 className="font-medium">{user.name}</h3>
                      <p className="text-sm text-gray-600">{user.email}</p>
                      <p className="text-sm text-gray-500">Age: {user.age}</p>
                      {user.createdAt && (
                        <p className="text-xs text-gray-400">
                          Created: {user.createdAt.toDate().toLocaleString()}
                        </p>
                      )}
                    </div>
                    <div className="flex space-x-1">
                      <button
                        onClick={() => {
                          const newName = prompt('Enter new name:', user.name);
                          if (newName && user.id) {
                            updateUser(user.id, newName);
                          }
                        }}
                        className="bg-blue-500 text-white px-2 py-1 rounded text-xs hover:bg-blue-600"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => {
                          if (user.id && confirm(`Delete ${user.name}?`)) {
                            deleteUser(user.id);
                          }
                        }}
                        className="bg-red-500 text-white px-2 py-1 rounded text-xs hover:bg-red-600"
                      >
                        Delete
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Instructions */}
      <div className="mt-8 bg-blue-50 p-4 rounded-lg">
        <h3 className="text-lg font-semibold mb-2">ðŸ“‹ Testing Instructions</h3>
        <ol className="list-decimal list-inside space-y-1 text-sm">
          <li>Click "Add Sample Data" to populate test data</li>
          <li>Try adding a new user using the form</li>
          <li>Test "Fetch Adults" to see query filtering in action</li>
          <li>Click "Start Real-time" and then add/edit/delete users in another tab to see live updates</li>
          <li>Use Edit/Delete buttons on individual users</li>
          <li>Check the browser console for detailed logs</li>
          <li>Open Firebase Console to see the data in your Firestore database</li>
        </ol>
      </div>
    </div>
  );
}
